import React, { useEffect } from 'react';

import { useAppDispatch } from '@utils/hooks';
import { removeAlert } from '@features/alert/alertSlice';

interface AlertProps {
	id: string;
	title: string;
	message: string;
	alertType: 'success' | 'warning' | 'danger';
}

const Alert: React.FC<AlertProps> = ({ id, title, message, alertType }) => {
	const dispatch = useAppDispatch();

	const setIcon = (alertType: string) => {
		const foundIcon = alertIcons.find(
			(alertIcon) => alertIcon.title === alertType
		);
		if (foundIcon) {
			return foundIcon.path;
		} else {
			return (
				<path
					fillRule='evenodd'
					d='M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z'
					clipRule='evenodd'
				/>
			);
		}
	};

	// remove alert after specified times
	useEffect(() => {
		setTimeout(() => {
			dispatch(removeAlert(id));
		}, 6000);
	}, []);

	return (
		<div
			key={id}
			className={`h-full w-full max-h-32 max-w-xs fixed bottom-0 left-0 z-40 p-4 rounded-lg shadow-md bg-white border border-t-4 ${
				alertType === 'success'
					? 'border-teal-500'
					: alertType === 'warning'
					? 'border-purple-500'
					: alertType === 'danger'
					? 'border-red-300'
					: 'border-gray-700'
			} transform -translate-y-12 translate-x-24`}
		>
			<div className='w-full flex items-start justify-between'>
				<div className='flex'>
					<div
						className={
							alertType === 'success'
								? 'text-teal-500'
								: alertType === 'warning'
								? 'text-purple-500'
								: alertType === 'danger'
								? 'text-red-300'
								: 'text-gray-700'
						}
					>
						<svg
							xmlns='http://www.w3.org/2000/svg'
							className='mt-1 h-5 w-5'
							viewBox='0 0 20 20'
							fill='currentColor'
						>
							{setIcon(alertType)}
						</svg>
					</div>

					<div className='ml-2'>
						<h4 className='font-semibold text-gray-900'>{title}</h4>
						<p className='mt-2 text-sm text-gray-600'>{message}</p>
					</div>
				</div>
				<button
					onClick={() => dispatch(removeAlert(id))}
					className={`text-gray-400 rounded-md ring-gray`}
				>
					<svg
						xmlns='http://www.w3.org/2000/svg'
						className='svg-sm'
						viewBox='0 0 20 20'
						fill='currentColor'
					>
						<path
							fillRule='evenodd'
							d='M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z'
							clipRule='evenodd'
						/>
					</svg>
				</button>
			</div>
		</div>
	);
};

const alertIcons = [
	{
		title: 'danger',
		path: (
			<path
				fillRule='evenodd'
				d='M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z'
				clipRule='evenodd'
			/>
		),
	},
	{
		title: 'warning',
		path: (
			<path
				fillRule='evenodd'
				d='M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z'
				clipRule='evenodd'
			/>
		),
	},
	{
		title: 'success',
		path: (
			<path
				fillRule='evenodd'
				d='M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z'
				clipRule='evenodd'
			/>
		),
	},
];

export default Alert;
